<template>
  <div id="app">
    <p>Your secret message (for encrypt) OR encrypted message (for decrypt):</p>
    <textarea name="text" rows="12" cols="70" v-model="message"></textarea><br/>
    <p>Password:</p>
    <input type="text" name="password" size="56" v-model="password"/><br/>
    <input type="submit" name="encrypt" value="Encrypt" @click="encrypt">
    <input type="submit" name="decrypt" value="Decrypt" @click="decrypt"><br/>
    
    <p>Encrypted message</p>
    <textarea name="result" rows="12" cols="70" v-model="result"></textarea>
  </div>
</template>

<script>
import { Blowfish } from 'javascript-blowfish';

export default {
  name: 'App',
  data() {
    return {
      message: '',
      password: '',
      result: ''
    }
  },
  methods: {
    encrypt() {
      const bf = new Blowfish(this.password);
      this.result = bf.base64Encode(bf.encrypt(this.message));
    },
    decrypt() {
      const bf = new Blowfish(this.password);
      this.result = bf.decrypt(bf.base64Decode(this.message));
    }
  },

  /* if in parameters is present text, set it as value */
  created() {
    /* not use $router */
    const urlParams = new URLSearchParams(window.location.search);
    if (urlParams.has('text')) {
      this.message = urlParams.get('text');
    }
  }


  
};
</script>

